.containerParent {
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
.containerChild {
  grid-area: container;
}
body .header .container .header__nav-panel__goods .dropMenu {
  width: 60%;
  display: flex;
  flex-wrap: wrap;
  position: absolute;
  z-index: 10;
  padding: 0 25px 25px 0;
  background-color: #9e79ac;
}
body .header .container .header__nav-panel__goods .dropMenu li {
  margin: 20px 0 0 25px;
  width: 30%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
body .header .container .header__nav-panel__goods .dropMenu li a {
  display: flex;
  justify-content: flex-start;
  align-content: center;
  font: 1em/1.25em "PTSans-Bold";
  color: #fff;
  text-align: left;
  border: none;
  background-color: #9e79ac;
}
body .header .container .header__nav-panel__goods .dropMenu li a i {
  line-height: 1.25em;
}
body .header .container .header__nav-panel__goods .dropMenu li a:hover {
  text-decoration: underline;
  background: #9e79ac;
}
body .header .container .header__nav-panel__goods .dropMenu li ul {
  display: flex;
  flex-direction: column;
}
body .header .container .header__nav-panel__goods .dropMenu li ul li {
  margin: 0 0 0 25px;
  width: 100%;
}
body .header .container .header__nav-panel__goods .dropMenu li ul li a {
  font: 1em/1.25em "PTSans-Regular";
}
body .header .container .header__nav-panel__goods .medicalDevices,
body .header .container .header__nav-panel__goods .cosmetics {
  margin-left: -30%;
  left: 37%;
}
body .header .container .header__nav-panel__goods .kidsGoods {
  margin-left: -30%;
  left: 50%;
}
body .header .container .header__nav-panel__goods .medicalEquipment,
body .header .container .header__nav-panel__goods .vitamins {
  margin-right: -30%;
  right: 37%;
}
body {
  font-size: 18px;
  line-height: 20px;
  color: #474747;
}
body .header {
  width: 100%;
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
body .header .container {
  grid-area: container;
  display: grid;
  grid-template-areas: ". headline headline" "logo search cabinet" "navPanel navPanel navPanel" "secondNav secondNav secondNav";
  grid-template-columns: 25% 45% 30%;
}
body .header .container .header__headline {
  grid-area: headline;
}
body .header .container .header__headline p {
  font: 1em/30px "PTSans-Regular";
  color: #474747;
  text-align: center;
  background-color: #ececec;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}
body .header .container .header__logo {
  grid-area: logo;
}
body .header .container .header__logo a,
body .header .container .header__logo span {
  display: block;
  width: 100%;
  height: 100%;
  background: url("../img/logo.png") no-repeat 50% 100%;
  background-size: contain;
}
body .header .container .header__mainSearch {
  grid-area: search;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}
body .header .container .header__mainSearch .search {
  width: 100%;
  margin-top: 70px;
  text-align: center;
  position: relative;
}
body .header .container .header__mainSearch .search input {
  width: 95%;
  height: 45px;
  padding-left: 25px;
  font: 1.125em/45px "PTSans-Regular";
  color: #9e9e9e;
  border: 3px solid #00929e;
  border-radius: 50px;
  background: transparent;
  outline: none;
}
body .header .container .header__mainSearch .search i {
  font-size: 1.5em;
  position: absolute;
  right: 20px;
  top: 13px;
  color: #00929e;
}
body .header .container .header__mainSearch .numbers {
  margin: 40px 0 0;
  width: 70%;
  display: flex;
  align-items: center;
}
body .header .container .header__mainSearch .numbers .icon-auricular-phone-symbol-in-a-circle {
  display: none;
}
body .header .container .header__mainSearch .numbers ul {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
body .header .container .header__mainSearch .numbers ul li .icon-auricular-phone-symbol-in-a-circle {
  display: block;
  font-size: 3em;
  color: #00929e;
}
body .header .container .header__mainSearch .numbers ul li a {
  font: 1.625em/2em "PTSans-Regular";
  color: #474747;
  text-decoration: none;
}
body .header .container .header__mainSearch .workTime {
  margin: 45px 0 0;
  width: 30%;
  display: flex;
  justify-content: flex-end;
}
body .header .container .header__mainSearch .workTime i {
  font-size: 1.5em;
  line-height: 1.725em;
  color: #00929e;
}
body .header .container .header__mainSearch .workTime ul {
  margin-left: 20px;
}
body .header .container .header__mainSearch .workTime ul li span {
  font: 0.75em/1em "PTSans-Regular";
  white-space: nowrap;
}
body .header .container .header__cabinet {
  grid-area: cabinet;
  margin-top: 70px;
}
body .header .container .header__cabinet ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
body .header .container .header__cabinet ul li a {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-decoration: none;
}
body .header .container .header__cabinet ul li a span {
  display: inline-block;
  font: 1.125em/1.125em "PTSans-Regular";
  color: #474747;
}
body .header .container .header__cabinet ul li a span:nth-child(1):hover {
  text-decoration: none;
}
body .header .container .header__cabinet ul li a span:nth-child(2) {
  width: 100%;
  text-align: center;
}
body .header .container .header__cabinet ul li a span i {
  padding: 0 20px;
  font-size: 3em;
  line-height: 1.125em;
  color: #00929e;
}
body .header .container .header__cabinet ul li a span:hover {
  text-decoration: underline;
  cursor: pointer;
}
body .header .container .header__cabinet ul li:nth-child(1) {
  width: 45%;
  margin: 0 0 0 10px;
}
body .header .container .header__cabinet ul li:nth-child(2) {
  width: 45%;
}
body .header .container .header__cabinet ul li:nth-child(3) {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
body .header .container .header__cabinet ul li:nth-child(3) a {
  margin-top: 50px;
  padding: 5px;
  width: 75%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font: 1.125em/1.1em "PTSans-Regular";
  color: #474747;
  text-align: center;
  text-decoration: none;
  border: 1px solid #00929e;
  border-radius: 5px;
}
body .header .container .header__cabinet ul li:nth-child(3) a span i {
  margin: 0 0 20px;
  padding: 0;
  font-size: 2em;
  color: #7d3daf;
  border-radius: 5px;
}
body .header .container .header__nav-panel {
  grid-area: navPanel;
  margin-top: 25px;
  display: flex;
  flex-direction: column;
}
body .header .container .header__nav-panel .mobileDrop {
  display: none;
}
body .header .container .header__nav-panel__major ul {
  display: flex;
  justify-content: space-between;
}
body .header .container .header__nav-panel__major ul li {
  width: 16.67%;
}
body .header .container .header__nav-panel__major ul li a {
  display: flex;
  justify-content: center;
  width: 100%;
  font: 1.125em/50px "PTSans-Bold";
  color: #fff;
  text-decoration: none;
  background-color: #176975;
  border-top: 1px solid #176975;
  box-sizing: border-box;
}
body .header .container .header__nav-panel__major ul li a:hover {
  transition: .5s;
  background-color: #9e79ac;
}
body .header .container .header__nav-panel__major ul li:nth-child(1) a {
  border-left: 1px solid #176975;
  border-right: 1px solid #77c9cc;
}
body .header .container .header__nav-panel__major ul li:nth-child(2) a {
  border-left: 1px solid #77c9cc;
  border-right: 1px solid #77c9cc;
}
body .header .container .header__nav-panel__major ul li:nth-child(3) a {
  border-left: 1px solid #77c9cc;
  border-right: 1px solid #77c9cc;
}
body .header .container .header__nav-panel__major ul li:nth-child(4) a {
  border-left: 1px solid #77c9cc;
  border-right: 1px solid #77c9cc;
}
body .header .container .header__nav-panel__major ul li:nth-child(5) a {
  border-left: 1px solid #77c9cc;
  border-right: 1px solid #77c9cc;
}
body .header .container .header__nav-panel__major ul li:nth-child(6) a {
  border-left: 1px solid #77c9cc;
  border-right: 1px solid #176975;
}
body .header .container .header__nav-panel__goods ul {
  display: flex;
  justify-content: space-between;
}
body .header .container .header__nav-panel__goods ul li {
  width: 25%;
}
body .header .container .header__nav-panel__goods ul li span {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  font: 1.125em/50px "PTSans-Regular";
  color: #0e3a40;
  text-decoration: none;
  background-color: #77c9cc;
  border: 1px solid #176975;
  box-sizing: border-box;
}
body .header .container .header__nav-panel__goods ul li span .active {
  background: #9e79ac;
}
body .header .container .header__nav-panel__goods ul li span:hover {
  cursor: pointer;
}
body .header .container .header__nav-panel__goods ul li:hover span {
  color: #fff;
  background-color: #9e79ac;
  transition: .5s;
}
body .header__nav-panel__mobile {
  height: 100%;
  width: 290px;
  position: absolute;
  top: 0;
  left: -300px;
  background: #0e3a40;
}
body .header__nav-panel__mobile .header__nav-panel__goodsMobile ul {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
body .header__nav-panel__mobile .header__nav-panel__goodsMobile ul li a {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  font: 1.375em/50px "PTSans-Regular";
  color: #0e3a40;
  text-decoration: none;
  background-color: #77c9cc;
  border-top: 1px solid #176975;
  border-bottom: 1px solid #176975;
}
body .header__nav-panel__mobile .header__nav-panel__goodsMobile ul li a i {
  margin: 0 0 0 25px;
}
body .header__nav-panel__mobile .header__nav-panel__goodsMobile ul li:nth-child(1) a {
  background: url("../img/main_slider.jpg") no-repeat;
  background-size: cover;
}
body .header__nav-panel__mobile .header__nav-panel__goodsMobile ul li:nth-child(1) a img {
  margin: 10px 0 10px 25px;
  width: 50%;
}
body .header__nav-panel__mobile .header__nav-panel__majorMobile ul {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
body .header__nav-panel__mobile .header__nav-panel__majorMobile ul li a {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  font: 1.375em/50px "PTSans-Bold";
  color: #fff;
  text-decoration: none;
  background-color: #176975;
  border-top: 1px solid #77c9cc;
  border-bottom: 1px solid #77c9cc;
}
body .header__nav-panel__mobile .header__nav-panel__majorMobile ul li a i {
  margin: 0 0 0 25px;
}
